document.querySelectorAll('.question__open-answer').forEach(
    (btn) => btn.addEventListener('click', () => {
        const ans = btn.parentElement.parentElement.lastElementChild;
        ans.classList.toggle('_active');
        btn.classList.toggle('_active');
    })
);
document.querySelectorAll('.button_try').forEach(
    (btn) => btn.addEventListener('click', () => {
        const modal = document.querySelectorAll('.modal-try')[0];
        modal.classList.toggle('_active');
        const body = document.getElementsByTagName('body')[0];
        body.classList.toggle('_scroll-hidden');
    })
);
const closeBtn = document.querySelectorAll('.modal-try__close')[0];
closeBtn.addEventListener('click', () => {
    const modal = document.querySelectorAll('.modal-try')[0];
    modal.classList.toggle('_active');
    const body = document.getElementsByTagName('body')[0];
        body.classList.toggle('_scroll-hidden');
})